/* spartan-300 - latin */
@font-face {
    font-family: "Spartan";
    font-style: normal;
    font-weight: 300;
    src: url("./fonts/spartan-v10-latin-300.eot"); /* IE9 Compat Modes */
    src: local(""), url("./fonts/spartan-v10-latin-300.eot?#iefix") format("embedded-opentype"),
        /* IE6-IE8 */ url("./fonts/spartan-v10-latin-300.woff2") format("woff2"),
        /* Super Modern Browsers */ url("./fonts/spartan-v10-latin-300.woff") format("woff"),
        /* Modern Browsers */ url("./fonts/spartan-v10-latin-300.ttf") format("truetype"),
        /* Safari, Android, iOS */ url("./fonts/spartan-v10-latin-300.svg#Spartan") format("svg"); /* Legacy iOS */
}
